.Movie .item {
    border: 1px solid black;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
}

.Movie .info {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
    -ms-flex-pack: distribute;
        justify-content: space-around;
    -ms-flex-align: center;
        align-items: center;
    padding-top: 0.25rem;
    padding-bottom: 0.25rem;
    cursor: pointer;
}

.Movie .info div {
    width: 25%;
}

.Movie .info .score {
    padding-left: 1rem;
}

.Movie .button {
    margin-bottom: 0;
}

.Movie .button-group {
    margin-left: 1rem;
    margin-right: 1rem;
}

.Movie .credits-button {
    margin-bottom: 0.75rem;
}


